<template>
    <div id="example">
        <vicky-header fixed>
        <router-link :to="{path: '/'}" slot="leftPart"></router-link>
        <span slot="centerPart">vickyLoading-</span>
        </vicky-header>
        <div class="demo-block-title">
            <span>loading</span>
        </div>
        <vicky-button type="primary" @click="loading1">loading</vicky-button>

        <div class="demo-block-title">
            <span>loading</span>
        </div>
        <vicky-button type="primary" @click="loading2">loading</vicky-button>

        <vicky-loading :text="text" :showLoading="loadingStatus"></vicky-loading>
    </div>
</template>
<script>
export default{
  data () {
    return {
      text: '',
      loadingStatus: false
    }
  },
  methods: {
    loading1 () {
      this.text = ''
      this.loadingStatus = true
      setTimeout(() => {
        this.loadingStatus = false
      }, 1000)
    },
    loading2 () {
      this.text = ''
      this.loadingStatus = true
      setTimeout(() => {
        this.loadingStatus = false
      }, 1000)
    }
  }
}
</script>



