<template>
    <div id="example">
        <vicky-header>
            <router-link to="/" slot="leftPart"></router-link>
            <span slot="centerPart"></span>
        </vicky-header>

        <div class="demo-block-title">
            <span></span>
        </div>
        <vicky-button type="primary" @click="oneBtnDialog"></vicky-button>
        <vicky-dialog :showDialog="oneBtnStatus" @click="oneBtnEvent">1</vicky-dialog>

        <div class="demo-block-title">
            <span></span>
        </div>
        <vicky-button type="primary" @click="twoBtnDialog"></vicky-button>
        <vicky-dialog :showDialog="twoBtnStatus" @click="twoBtnEvent" isTwo>22222</vicky-dialog>

        <div class="demo-block-title">
            <span></span>
        </div>
        <vicky-button type="primary" @click="hasTitleDialog"></vicky-button>
        <vicky-dialog :showDialog="titleStatus" @click="titleBtnEvent" isTwo :title="titleContent">1</vicky-dialog>
    </div>
</template>
<script>
export default{
  data () {
    return {
      oneBtnStatus: false,
      twoBtnStatus: false,
      titleStatus: false,
      titleContent: ''
    }
  },
  methods: {
    oneBtnDialog () {
      this.oneBtnStatus = true
    },
    twoBtnDialog () {
      this.twoBtnStatus = true
    },
    hasTitleDialog () {
      this.titleStatus = true
    },
    oneBtnEvent (isConfirm) {
      this.oneBtnStatus = false
      if (isConfirm) {
        console.log('')
      }
    },
    twoBtnEvent (isConfirm) {
      this.twoBtnStatus = false
      if (isConfirm) {
        console.log('')
      } else {
        console.log('')
      }
    },
    titleBtnEvent (isConfirm) {
      this.titleStatus = false
      if (isConfirm) {
        console.log('')
      }
    }
  }
}
</script>



