<template>
    <div id="example">
        <vicky-header fixed>
            <router-link :to="{path: '/'}" slot="leftPart"></router-link>
            <span slot="centerPart">header - </span>
        </vicky-header>
        <div class="demo-block-title">
        <span></span>
        </div>
        <vicky-header :fixed="false">
            <span slot="centerPart"></span>
        </vicky-header>

        <div class="demo-block-title">
            <span></span>
        </div>
        <vicky-header :fixed="false">
            <span slot="leftPart"></span>
            <span slot="centerPart"></span>
        </vicky-header>

        <div class="demo-block-title">
            <span></span>
        </div>
        <vicky-header :fixed="false">
            <router-link to="/" slot="leftPart" ></router-link>
            <span slot="centerPart"></span>
        </vicky-header>

        <div class="demo-block-title">
            <span></span>
        </div>
        <vicky-header :fixed="false">
            <span slot="rightPart"></span>
            <span slot="centerPart"></span>
        </vicky-header>

        <div class="demo-block-title">
            <span></span>
        </div>
        <vicky-header :fixed="false">
            <span slot="leftPart"></span>
            <span slot="centerPart"></span>
            <span slot="rightPart"></span>
        </vicky-header>

    </div>
</template>
<script>
export default{
  methods: {
  }
}
</script>



