<template>
    <div id="example">
        <vicky-header>
            <router-link to="/" slot="leftPart"></router-link>
            <span slot="centerPart"></span>
        </vicky-header>
        <div class="demo-block-title">
            <span> - </span>
        </div>

        <input type="text" placeholder="" @click="inputFocus" :value="selectorGroupDatas.chooseItem.status?selectorGroupDatas.chooseItem.content:''">
        <vicky-selector :selectorDatasLists="selectorGroupDatas"></vicky-selector>
    </div>
</template>
<script>
export default{
  data () {
    return {
      selectorGroupDatas: {
        chooseItem: {
          status: false, // 
          content: ''// input value
        }, // inputvalue
        showSelector: false, // selector
        showMask: false, // 
        ItemDatas: [// selector 
          {index: 0, textContent: ''},
          {index: 1, textContent: ''},
          {index: 2, textContent: ''},
          {index: 3, textContent: ''},
          {index: 4, textContent: ''},
          {index: 5, textContent: ''},
          {index: 6, textContent: ''},
          {index: 7, textContent: ''}
        ]
      }
    }
  },
  methods: {
    inputFocus () {
      this.selectorGroupDatas.showSelector = !this.selectorGroupDatas.showSelector
      this.selectorGroupDatas.showMask = !this.selectorGroupDatas.showMask
      this.selectorGroupDatas.chooseItem.status = false
    }
  }
}
</script>
<style lang="stylus" scoped>
    input
        width 100%
        padding 13px 0
        line-height 24px
        font-size 16px
        text-indent 10px
        border none
        border-bottom 1px solid #ccc
        background-color #fff
        &:focus
            outline 0
</style>



